@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: "Avenir";
  src: url("./fonts/Avenir LT Std 55 Roman.otf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Avenir";
  src: url("./fonts/Avenir LT Std 65 Medium.otf");
  font-weight: medium;
  font-style: normal;
}
@font-face {
  font-family: "Avenir";
  src: url("./fonts/Avenir LT Std 85 Heavy.otf");
  font-weight: bold;
  font-style: normal;
}

h1 {
  font-size: calc(2.5rem + 1.875vw);
  @apply font-sans font-bold text-white leading-[120%];
}

h2 {
  font-size: calc(2rem + 1.25vw);
  @apply font-sans font-bold text-newBlack leading-[120%];
}

h3 {
  font-size: calc(2rem + 0.78125vw);
  @apply font-sans font-bold text-newBlack leading-[120%];
}

p {
  font-size: calc(0.5rem + 0.625vw);
  @apply font-sans text-grey leading-[150%];
}

@layer components {
  .homeBg {
    background: url("./assets/banner.png"), lightgray 50% / cover no-repeat,
      linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0.36%, rgba(0, 0, 0, 0) 91.79%),
      linear-gradient(88deg, #ee1a24 23.95%, rgba(238, 26, 36, 0) 98.29%),
      url("./assets/home.jpg"), lightgray 50% / cover no-repeat;
    @apply w-full h-[100vh] bg-cover bg-center relative;
  }

  .row {
    @apply flex flex-row;
  }

  .col {
    @apply flex flex-col;
  }

  .logo {
    font-size: calc(1rem + 1.5vw);
    @apply font-sans text-newBlack;
  }

  .navLink {
    font-size: calc(0.5rem + 0.625vw);
    @apply font-sans;
  }

  .homeText {
    font-size: calc(1.5rem + 0.78125vw);
    @apply font-sans font-bold text-white;
  }

  .btn {
    font-size: calc(0.5rem + 0.46875vw);
    @apply font-sans font-bold text-white cursor-pointer bg-niceBlue px-[1.7187500000000002vw] py-[0.8vw] rounded-md;
  }

  .description {
    font-size: calc(0.5rem + 0.78125vw);
    @apply font-sans text-grey leading-[150%];
  }

  .semiTitle {
    font-size: calc(0.75rem + 0.625vw);
    @apply font-sans text-newBlack leading-[120%];
  }

  .formation {
    font-size: calc(1.25rem + 0.9375vw);
    @apply font-sans text-white font-bold leading-[120%];
  }

  .formationSemiTitle {
    font-size: calc(1rem + 0.9375vw);
    @apply font-sans text-newBlack font-bold leading-[120%];
  }

  .statTitle {
    font-size: calc(0.75rem + 0.78125vw);
    @apply font-sans text-newBlack leading-[140%];
  }

  .statText {
    font-size: calc(0.65rem + 0.625vw);
    @apply font-sans text-grey leading-[140%];
  }
}
